import React, { useEffect, useState } from "react";
import "./SubmissionForm.scss";
import { globals } from "../../globals"

type SubmissionFormProps = {
  title?: string,
  anchor?: string
  iframe: { 
    url: string,
    frameTitle: string,
  },
  customClass: string
}

const SubmissionForm = ({ title, anchor = "submissionForm", iframe: { url, frameTitle}, customClass } : SubmissionFormProps) => {
  const iframeId = "iframe_" + Math.floor(Math.random() * 1001)
  const [iframe, setIframe] = useState<HTMLIFrameElement | null>(null)

  useEffect(() => {
    const iframe = document.getElementById(iframeId) as HTMLIFrameElement;
    console.log(iframe);
    if (iframe) {
      setIframe(iframe)
    }
  }, [])

  useEffect(() => {
    if (iframe)
      iframe.onload = function(){
        iframe.style.height = iframe.contentWindow?.document.body.scrollHeight + 'px';
      }
  }, [iframe])

  return (
    <div className={`eventDetails ${customClass}`} id={anchor}>
      <div className="content">
        {title && <h2>{title}</h2>}
        <iframe id="iframeId" src={url} title={frameTitle} width="100%" height="2000" frameBorder="0">Loadingâ€¦</iframe>
      </div>
    </div>
  );
};

export default SubmissionForm;
